import {Api} from "../api/api";
import {stopSubmit} from "redux-form";
import {setMainUserInfo} from "./ProfileReducer";
import {setFriendDataSucsess} from "./UsersReducer";
import {setInitialized, setInitializedSucsess} from "./appReducer";

let SET_AUTH_USER_DATA = "auth/SET_AUTH_USER_DATA"


let authDefaulState = {
    login: null,
    mainUserId: null,
    email: null,
    isAuth: false,
}

export const authReducer = (state = authDefaulState, action) => {
    let copyState;
    switch (action.type) {
        case SET_AUTH_USER_DATA:
            copyState = {
                ...state,
                ...action.userAuthdata
            }
            return copyState
        default:
            return state
    }
}

export let setAuthUserDataSucsess = (login, email, mainUserId, isAuth) => ({
    type: SET_AUTH_USER_DATA,
    userAuthdata: {login, mainUserId, email, isAuth}
})

export let setAppData = () => async (dispatch) => {
    dispatch(setInitializedSucsess(true))
    let responseUserAuth = await Api.auth.getAuthMe()
    if (responseUserAuth.resultCode === 0) {
        dispatch(setAuthUserDataSucsess(responseUserAuth.data.login, responseUserAuth.data.email, responseUserAuth.data.id, true))
        let responsUserProfile = await Api.profile.getUserProfile(responseUserAuth.data.id)
        let responseUserProfileStatus = await Api.profile.getUserProfileStatus(responseUserAuth.data.id)
        dispatch(setMainUserInfo({...responsUserProfile, responseUserProfileStatus}))
    }
    dispatch(setInitializedSucsess(false))
}
export let setAuthUserData = () => async (dispatch) => {
    let response = await Api.auth.getAuthMe()
    if (response.resultCode === 0) {
        dispatch(setAuthUserDataSucsess(response.data.login, response.data.email, response.data.id, true))
    }
}
export let logIn = (login, password) => async (dispatch) => {
    let response = await Api.auth.logIn(login, password)
    if (response.data.resultCode === 0) {
        dispatch(setInitialized())
    } else {
        dispatch(stopSubmit("Login", {_error: response.data.messages}))
    }
}
export let logOut = () => async (dispatch) => {
    let response = await Api.auth.logOut()
    if (response.data.resultCode === 0) {
        dispatch(setAuthUserDataSucsess(null, null, null, false))
    }
}


window.authDefaulState = authDefaulState


