import {Api} from "../api/api";

let ADD_POST = "profile/ADD-POST"
let DELETE_POST = "profile/DELETE_POST"
let SET_MAIN_USER_INFO = "profile/SET_MAIN_USER_INFO"
let GET_USER_PROFILE_STATUS_SUCSESS = "profile/GET_USER_PROFILE_STATUS_SUCSESS"

let ProfileDefaulState = {
    userInfoDate: {
        name: "",
        status: ""
    }
    ,
    postMessageData: [
        {id: 1, message: "Hi, how are you?", likeCounter: "20", user: "vlad"},
    ],
    mainAvatar: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT0OKqoPKtc6zG0u7pS8C3klRQ-7n0KLvYTaQ&usqp=CAU",
}

export const ProfileReducer = (state = ProfileDefaulState, action) => {
    let copyState;
    switch (action.type) {
        case ADD_POST:
            let newPost = {
                message: action.text,
                likeCounter: 5,
                user: "vlad",
                id: state.postMessageData[state.postMessageData.length - 1].id + 1
            }
            copyState = {
                ...state,
                postMessageData: [...state.postMessageData, newPost],
            }
            return copyState
        case DELETE_POST:
            copyState = {
                ...state
            }
            copyState.postMessageData.pop()
            return copyState
        case SET_MAIN_USER_INFO:
            copyState = {
                ...state
            }
            copyState.userInfoDate.name = action.mainUserInfo.fullName
            copyState.userInfoDate.status = action.mainUserInfo.responseUserProfileStatus
            return copyState
        case GET_USER_PROFILE_STATUS_SUCSESS:
            copyState = {
                ...state
            }
            copyState.userInfoDate.status = action.UserTextStatus
            return copyState
    }
    return state
}

export let addPostSucsess = (text) => ({
    type: ADD_POST,
    text
})
export let deletePostSucsess = () => ({
    type: DELETE_POST

})
export let setMainUserInfo = (mainUserInfo) => ({
    type: SET_MAIN_USER_INFO,
    mainUserInfo
})
export let setUserProfileStatusSucsess = (UserTextStatus) => ({
    type: GET_USER_PROFILE_STATUS_SUCSESS,
    UserTextStatus
})


export let setUserProfile = (userId, toogleIsLoading) => async (dispatch) => {
    if (userId) {
        await Api.profile.getUserProfile(userId)
        dispatch(toogleIsLoading(true))
        await Api.profile.getUserProfileStatus(userId)
        dispatch(toogleIsLoading(false))

    }
}
export let getUserProfileStatus = (mainUserId) => async (dispatch) => {
    if (mainUserId) {
        let response = await Api.profile.getUserProfileStatus(mainUserId)
        dispatch(setUserProfileStatusSucsess(response))
    }

}
export let setUserProfileStatus = (text) => async (dispatch) => {
    await Api.profile.setUserProfileStatus(text)
    dispatch(setUserProfileStatusSucsess(text))
}

window.ProfileDefaulState = ProfileDefaulState


