import './App.css';
import Footer from "./components/Footer/Footer";
import {Route, withRouter} from "react-router-dom";
import News from "./components/appWrapperContent/News/News";
import Music from "./components/appWrapperContent/Music/Music";
import Setting from "./components/appWrapperContent/Setting/Setting";

import SideBarContainer from "./components/sideBar/sideBarContainer";

import Login from "./components/appWrapperContent/Login";
import {Component, useEffect, useState} from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import React from "react";
import {setInitialized} from "./redux/appReducer";
import Preloader from "./components/Preloader/Preloader";
import {getInitialized} from "./Selectors/Selectors";
import Header from "./components/Header/Header";


const DialogsContainer = React.lazy(() => import('./components/appWrapperContent/Dialogs/DialogsContainer'));
const UsersContainer = React.lazy(() => import('./components/appWrapperContent/Users/UsersContainer'));
const ProfileContainer = React.lazy(() => import('./components/appWrapperContent/Profile/ProfileContainer'));


let App = (props) => {
    useEffect(() => {
        props.setInitialized()
    }, [])
    if (props.initialized) {
        return <Preloader/>
    }
    return (
        <div className="appWrapper">
            <Header/>
            <SideBarContainer/>
            <div className="appWrapperContent">
                <React.Suspense fallback={<Preloader/>}>
                    <Route path={"/profile/:userId?"} render={() =>
                        <ProfileContainer/>}
                    />
                    <Route path={"/dialogs"} render={() =>
                        <DialogsContainer/>}
                    />
                    <Route path={"/users"} render={() =>
                        <UsersContainer/>}
                    />
                    <Route path={"/feed"} render={() => <News/>}/>
                    <Route path={"/music"} render={() => <Music/>}/>
                    <Route path={"/setting"} render={() => <Setting/>}/>
                    <Route path={"/login"} render={() => <Login/>}/>
                </React.Suspense>
            </div>
            <Footer/>
        </div>
    )
}


let mapStateToProps = state => {
    return {
        initialized: getInitialized(state)
    }
}

export default compose(
    withRouter,
    connect(mapStateToProps, {setInitialized})
)(App);






