import {setAppData, setAuthUserData} from "./authReducer";

let SET_INITIALIZIED = "app/SET_INITIALIZIED"

let appDefaulState = {
    initialized: false
}

export const appReducer = (state = appDefaulState, action) => {
    let copyState;
    switch (action.type) {
        case SET_INITIALIZIED:
            copyState = {
                ...state,
                initialized: action.boolean
            }
            return copyState
        default:
            return state
    }
}

export let setInitializedSucsess = (boolean) => ({
    type: SET_INITIALIZIED,
    boolean
})

export let setInitialized = () => (dispatch) => {
    let promiseAuth = dispatch(setAppData())
    Promise.all([promiseAuth]).then((response) => {

    })
}
