import {Api} from "../api/api";

let FOLLOW = "users/FOLLOW"
let UNFOLLOW = "users/UNFOLLOW"
let SET_USERS = "users/SET_USERS"
let SET_TOTAL_USERS_COUNT = "users/SET_TOTAL_USERS_COUNT"
let SET_CURRENT_PAGE = "users/SET_CURRENT_PAGE"
let TOOGLE_IS_LOADING = "users/TOOGLE_IS_LOADING"
let TOOGLE_IS_FOLLOWING_IN_PROGRESS_USER = "users/TOOGLE_IS_FOLLOWING_IN_PROGRESS_USER"
let SET_FRIEND_DATA = "users/SET_FRIEND_DATA"

let UsersDefaulState = {
    totalUsersCount: 0,
    currentPage: 1,
    numberOfUsersOnPage: 5,
    isLoading: false,
    isFollowingInProgress: null,
    usersInProgressFollowing: [],
    usersData: [],
    followingUsersData: []
}

export const UsersReducer = (state = UsersDefaulState, action) => {
    let copyState;
    switch (action.type) {
        case FOLLOW:
            copyState = {
                ...state
            }
            for (let u of copyState.usersData) {
                if (action.userId === u.id) {
                    u.followed = true
                }
            }
            return copyState

        case UNFOLLOW:
            copyState = {
                ...state
            }
            for (let u of copyState.usersData) {
                if (action.userId === u.id) {
                    u.followed = false
                }
            }
            return copyState
        case SET_USERS:
            copyState = {
                ...state,
                usersData: [...action.users]
            }
            return copyState
        case SET_TOTAL_USERS_COUNT:
            copyState = {
                ...state,
                totalUsersCount: action.count
            }
            return copyState
        case SET_CURRENT_PAGE:
            copyState = {
                ...state,
                currentPage: action.currentPage
            }
            return copyState
        case TOOGLE_IS_LOADING:
            copyState = {
                ...state,
                isLoading: action.isLoading
            }
            return copyState
        case TOOGLE_IS_FOLLOWING_IN_PROGRESS_USER:
            copyState = {
                ...state,
                usersInProgressFollowing: action.isFollowingInProgress
                    ? [...state.usersInProgressFollowing, action.userId]
                    : state.usersInProgressFollowing.filter(id => id != action.userId)
            }
            return copyState
        case SET_FRIEND_DATA:
            copyState = {
                ...state,
                followingUsersData: action.data
            }
            return copyState
        default:
            return state
    }
}

export let followSucsess = (userId, user) => ({
    type: FOLLOW,
    userId,
    user
})
export let unfollowSucsess = (userId, user) => ({
    type: UNFOLLOW,
    userId,
    user
})
export let setUsers = (users) => ({
    type: SET_USERS,
    users
})
export let setTotalUsersCount = (count) => ({
    type: SET_TOTAL_USERS_COUNT,
    count
})
export let setCurrentPage = (currentPage) => ({
    type: SET_CURRENT_PAGE,
    currentPage
})
export let toogleIsLoading = (isLoading) => ({
    type: TOOGLE_IS_LOADING,
    isLoading
})
export let toogleFollowingInProgressUser = (isFollowingInProgress, userId) => ({
    type: TOOGLE_IS_FOLLOWING_IN_PROGRESS_USER,
    isFollowingInProgress, userId
})
export let setFriendDataSucsess = (data) => ({
    type: SET_FRIEND_DATA,
    data
})
export const getUsers = (numberOfUsersOnPage, currentPage) => async (dispatch) => {
    dispatch(toogleIsLoading(true))
    let response = await Api.users.getUsers(numberOfUsersOnPage, currentPage)
    dispatch(setUsers(response.items))
    dispatch(setTotalUsersCount(response.totalCount))
    dispatch(toogleIsLoading(false))
}

export const toogleFollowStatus = (el) => async (dispatch) => {
    dispatch(toogleFollowingInProgressUser(true, el.id))
    if (el.followed) {
        await Api.follow.unfollowUser(el.id)
        dispatch(unfollowSucsess(el.id, el))
        dispatch(toogleFollowingInProgressUser(false, el.id))
        let response = await Api.users.getFollowingUsers()
        dispatch(setFriendDataSucsess(response.items))
    } else {
        await Api.follow.followUser(el.id)
        dispatch(followSucsess(el.id, el))
        dispatch(toogleFollowingInProgressUser(false, el.id))
        let response = await Api.users.getFollowingUsers()
        dispatch(setFriendDataSucsess(response.items))
    }

}
export let setUserData = () => async (dispatch) => {
    let response = await Api.users.getFollowingUsers()
    dispatch(setFriendDataSucsess(response.items))
}
